<template>
  <div>
      <b-button block variant="outline-primary" @click="back()">
          <b-row>
            <b-col cols="1" ><b-icon icon="chevron-left" aria-hidden="true" class="font-weight-bold text-left"></b-icon> </b-col>
            <b-col cols="10" ><span class="font-weight-bold text-right">Connexion</span></b-col>
          </b-row>
      </b-button>
      <b-row  id="ecart2" align-h="center" class="font-weight-bold">
        <b-button variant="outline-white" disabled align-v="center" >
            <div v-if="this.$store.getters.getRole=='PHARMACIE'"><b-icon icon="building" aria-hidden="true"></b-icon><span> Pharmacie</span></div>
            <div v-if="this.$store.getters.getRole=='USAGER'"><b-icon icon="person" aria-hidden="true"></b-icon><span> Usager</span></div> 
        </b-button>
      </b-row>
              
        <b-container>
            <b-row  align-h="center" class="font-weight-bold">
               <form>


                  <b-row id="ecart3" v-if="isErrorMessage">
                    <b-col cols="2" >
                        <b-button variant="outline-white"  >
                          <b-icon icon="exclamation-circle-fill" variant="danger"></b-icon>
                        </b-button>
                    </b-col>
                    <b-col cols="10" class="text-wrap">{{errorMessage}}</b-col>
                  </b-row>

                  <b-row id="ecart2">
                    <b-col cols="2" >
                        <b-button variant="outline-white" disabled squared>
                          <b-icon icon="person-fill"></b-icon>
                        </b-button>
                    </b-col>
                    <b-col cols="10" ><b-input type="text" v-model="email" placeholder="Entrez votre email..."></b-input></b-col>
                  </b-row>
                  <b-row id="ecart2">
                    <b-col cols="2" >
                        <b-button variant="outline-white" disabled squared>
                          <b-icon icon="key-fill"></b-icon>
                        </b-button>
                    </b-col>
                    <b-col cols="10" ><b-input type="password" v-model="password" placeholder="Entrez votre mot de pass..."></b-input></b-col>
                  </b-row>
                  <b-row id="ecart2" v-if="this.creerCompteVal">
                    <b-col cols="2" >
                        <b-button variant="outline-white" disabled squared>
                          <b-icon icon="key-fill"></b-icon>
                        </b-button>
                    </b-col>
                    <b-col cols="10" ><b-input type="password" v-model="passwordConfirm" placeholder="Confirmer votre mot de pass..."></b-input></b-col>
                  </b-row>
                    <b-row id="ecart2" align-h="center" class="font-weight-bold">
                        <b-button type="button" v-on:click="connexion" variant="primary">
                          <b-spinner small type="grow" v-if="!connexionClique"></b-spinner>Connexion
                        </b-button>
                    </b-row>
                    <b-row  align-h="center" class="font-weight-bold text-primary" v-on:click="creerCompte">
                      <div id="ecart2" v-if="!this.creerCompteVal">Vous n'avez pas de compte?</div>
                      <div id="ecart2" v-if="this.creerCompteVal">J'ai dj un compte</div>
                    </b-row>
                    <b-row id="ecart2" align-h="center" class="font-weight-bold text-primary" v-on:click="motDePassOublier">
                     Mot de passe oubli?
                    </b-row>
                </form>    
            </b-row>          
        </b-container>
      
  </div>
</template>

<script>
import moment from "moment";
import {profilRef} from "../firebase"
import {fauth} from "../firebase"

export default {
  app: "Connexion",
  data(){
      return {
        creerCompteVal:false,
        email:"",
        password:"",
        passwordConfirm:"",
        errorMessage:"Mode passe incorrect!",
        isErrorMessage:false,
        profilConnecte: this.$store.getters.getProfilConnecte,
        connexionClique:true
        //timestamp: moment().valueOf().toString()
      };
    },
  methods: {
      back (){this.$store.commit('back');},

     connexion(){
       this.connexionClique=false;
        const self = this;
           if(!this.creerCompteVal){
                fauth.
                signInWithEmailAndPassword(this.email, this.password)
                    .then( async () => {
                      self.isErrorMessage=false;
                      
                      await profilRef.doc(self.email)
                        .get().then( async snapshot => {
                                        //var st=JSON.stringify(snapshot.data());
                                        this.profilConnecte= snapshot.data();
                                        self.profilConnecte.deniereConnexion=moment.now();

                                        profilRef.doc(self.email).set(self.profilConnecte).then(()=>{
                                          self.$store.commit('setProfilConnecte',self.profilConnecte);

                                              if(self.profilConnecte.role !=self.$store.getters.getRole){

                                                    self.errorMessage='Vous n\'tes pas '+ self.profilConnecte.role;
                                                    self.isErrorMessage=true;

                                                        setTimeout(()=>{
                                                              fauth.signOut().then(() => {
                                                                  self.$store.commit('setIsConnected',false);
                                                                   self.$router.push("/connexion-1/");
                                                              });

                                                        },3000);
                                              }else{
                                                    self.$store.commit('setIsConnected',true);
                                                    
                                                      if(self.profilConnecte.role=='PHARMACIE'){
                                                        self.$router.push("/pharmacie-detail/");
                                                        // this.$router.go({path: this.$router.path});
                                                      }
                                
                                                      if(self.profilConnecte.role=='USAGER'){
                                                        self.$router.push("/home");
                                                        // this.$router.go({path: this.$router.path});
                                                        
                                                      }
                                              }


                                        });
                                    }, (error) => {
                                              console.log(error);
                                    }
                              );
                      
                          // .where("email", "==", this.email).get().;

                    })
                    .catch(function (error) {
                        self.errorMessage=error.message;
                        self.isErrorMessage=true;

                        if(error.code == "auth/user-not-found"){
                          self.errorMessage="Ce compte n'existe pas!\n Creer un compte.";
                        }
                        if (error.code == "auth/wrong-password") {
                          self.errorMessage="Mot de passe incorrect!";
                        }                        
                    });
           }else{
                if(this.password==this.passwordConfirm){

                  fauth.
                  createUserWithEmailAndPassword(self.email, self.password)
                      .then(()=> {
                        self.isErrorMessage=false;
                          self.profilConnecte.email=self.email;
                          self.profilConnecte.role=self.$store.getters.getRole;
                          self.profilConnecte.creeLe=moment.now();

                          profilRef.doc(self.email).set(self.profilConnecte)
                          .then(() => {
  
                                self.$store.commit('setProfilConnecte',self.profilConnecte);
                          
                                if(self.profilConnecte.role=='PHARMACIE'){
                                  self.$router.push("/pharmacie-detail/");
                                  // this.$router.go({path: this.$router.path});
                                }
  
                                if(self.profilConnecte.role=='USAGER'){
                                  self.$router.push("/home");
                                  // this.$router.go({path: this.$router.path});
                                }
                            
                          })
  
  
                      })
                      .catch((error)=> {
                        
                        // Handle Errors here.
                        
                        var errorCode = error.code;
     
                        self.errorMessage=error.message;
                        self.isErrorMessage=true;
                        if (errorCode == "auth/weak-password") {
                          self.errorMessage="Le mot de passe est faible!";
                          
                        } 
                        if (errorCode == "auth/email-already-in-use") {
                          self.errorMessage="Ce compte existe dj! Connectez-vous.";
                        }
  
                      });

                }else{
                  this.isErrorMessage=true;
                  this.errorMessage="Le mot de passe est diffrent!";
                }
           }
          this.connexionClique=false;
      },

      creerCompte(){
        this.creerCompteVal=!this.creerCompteVal;
        this.isErrorMessage=false;},

      motDePassOublier(){
        this.isErrorMessage=false;
        this.$router.push("/reset-password/");}
  }
}
</script>

<style scoped lang="scss">

    #ecart2 {
        margin-top: 10%;
    }
    #ecart3 {
        background-color: rgba(220, 20, 60, 0.199);
        color:crimson;
        border-radius: 20px;
        // margin-top: 10%;
    }
</style>